<template>
  <div>
    <search-box :searchData="searchData" :table="table"></search-box>
    <div class="btn-block">
      <div class="btn-main mr15" @click="$exportDetail('/export/teacherApplyExport.do')">导出</div>
      <div class="btn-main" @click="dialog=true">导入应聘结果</div>
      <div class="btn-main-deepen ml15" @click="onClickSms()">群发短信</div>
    </div>
    <resize-table>
      <table class="table">
        <thead>
        <tr>
          <th width="3%">
            <el-checkbox v-model="checkAll" v-show="checkAll !== null"></el-checkbox>
            <span v-show="checkAll === null">-</span>
          </th>
          <th width="6%">姓名</th>
          <th width="10%">联系电话</th>
          <th width="8%">应聘岗位</th>
          <th width="10%">专业技术职务</th>
          <th width="10%">第一学历</th>
          <th width="7%">政治面貌</th>
          <th width="14%">现工作单位</th>
          <th width="7%">状态</th>
          <th width="9%">短信发送状态</th>
          <th width="9%">短信确认状态</th>
          <th width="9%">面试时间</th>
          <th width="9%">面试地点</th>
          <th width="11%">操作</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(item,key) in table.list">
          <td>
            <el-checkbox v-model="checkboxList[key]" v-show="item.messageStatus !== 'SUCCESS' && item.applyStatus==='INITIAL'"></el-checkbox>
            <span v-show="item.messageStatus === 'SUCCESS'||item.applyStatus!=='INITIAL' ">-</span>
          </td>
          <td>{{item.name}}</td>
          <td>{{item.phone}}</td>
          <td>{{$enums.TeacherApplyPosition.getName(item.position)}}</td>
          <td>{{item.professionPosition}}</td>
          <td>{{$enums.Education.getName(item.firstEducation)}}</td>
          <td>{{$enums.PoliticsStatus.getName(item.politicsStatus)}}</td>
          <td>{{item.lastWorkUnit}}</td>
          <td>{{$enums.TeacherApplyStatus.getName(item.applyStatus)}}</td>
          <td>{{$enums.MessageStatus.getName(item.messageStatus)}}</td>
          <td>{{$enums.MessageConfirm.getName(item.messageConfirm)}}</td>
          <td>
            <span v-show="item.messageConfirm === 'CONFIRMED'">{{item.interviewTime}}</span>
            <span v-show="item.messageConfirm !== 'CONFIRMED'">-</span>
          </td>
          <td class="text-overflow">
            <el-tooltip effect="dark" :content="item.interviewVenue" placement="right">
              <span v-show="item.messageConfirm === 'CONFIRMED'">{{item.interviewVenue}}</span>
            </el-tooltip>
            <span v-show="item.messageConfirm !== 'CONFIRMED'">-</span>
          </td>
          <td>
            <span @click="$router.push(`/personnel/recruitment/edit?id=${item.teacherApplyId}`)" class="oper">详情</span>
            <span class="oper" @click="storage(item)"
                  v-if="item.applyStatus === 'PASS'">档案入库</span>
          </td>
        </tr></tbody>
      </table>
    </resize-table>
    <no-data v-show="!table.noData"></no-data>
    <paging :data.sync="table"></paging>

    <!--短信发送-->
    <el-dialog title="短信发送"
               :visible.sync="dialogVisible"
               width="30%">
      <el-form label-width="80px"
               label-position="left" :model="form" ref="form" :rules="rules">
        <el-form-item label="面试时间" prop="interviewTime">
          <el-date-picker
                  v-model="form.interviewTime"
                  type="datetime"
                  value-format="yyyy-MM-dd HH:mm:ss"
                  placeholder="选择日期时间" style="width: 100%">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="面试地点" prop="interviewVenue">
          <el-input clearable v-model="form.interviewVenue"></el-input>
        </el-form-item>
      </el-form>
      <div style="text-align: center">
        <div class="btn-submit mr15 btn-Sms" @click="onSubmitInterview">确定</div>
        <div class="btn-clear" @click="dialogVisible = false">取消</div>
      </div>
    </el-dialog>

    <!--导入-->
    <el-dialog title="导入" class="dialog" :visible.sync="dialog" width="540px">
      <import-excel @change="change" type="应聘结果导入模板" ref="importExcel" />
      <div class="dialog_btns">
        <div @click="impFun" class="btn-submit btn-upload mr15">确认</div>
        <div @click="dialog = false" class="btn-clear">返回</div>
      </div>
    </el-dialog>
  </div>
</template>
<script>

  import mixins from '../../../assets/js/mixins'

  export default {
    mixins: [mixins.$exportDetail],
    data () {
      return {
        searchData: [
          {type: 'select', title: '应聘岗位', model: 'position', option: this.$enums.TeacherApplyPosition.list},
          {type: 'select', title: '应聘状态', model: 'applyStatus', option: this.$enums.TeacherApplyStatus.list},
          {type: 'select', title: '短信发送状态', model: 'messageStatus', option: this.$enums.MessageStatus.list},
          {type: 'select', title: '短信确认状态', model: 'messageConfirm', option: this.$enums.MessageConfirm.list},
          {type: 'input', placeholder: '姓名/联系电话'}],
        table: {
          url: '/teacherApply/queryTeacherApplyPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            position: null,
            applyStatus: null,
            messageStatus: null,
            messageConfirm: null,
            keywords: null
          }
        },
        // 表格多选框
        checkboxList: [false],
        // 短信
        dialogVisible: false,
        form: {
          phone: [],
          interviewTime: null,
          interviewVenue: null
        },
        rules: {
          interviewTime:
            {required: true, message: '请选择面试时间', trigger: 'change'},
          interviewVenue:
            {required: true, message: '请输入面试地点', trigger: 'blur'},
        },
        // 导入
        dialog: false,
        files: null
      }
    },
    watch: {
      'table.list': function () {
        this.table.list.forEach((e, i) => {
          this.table.list[i].selected = false
          this.checkboxList = new Array(this.table.list.length).fill(false)
        })
      },
      'dialog': function () {
        if (!this.dialog) {
          this.$refs.importExcel.$data.fileName = null
        }
      },
    },
    computed: {
      checkAll: {
        get: function () {
          if (this.table.list.length < 1 || this.table.list.every(e=> e.applyStatus === 'PASS'||e.messageStatus === 'SUCCESS')) {
            return null
          } else if (this.table.list.every(item => item.messageStatus === 'SUCCESS' || item.applyStatus!=='INITIAL')) {
            return false
          }
          for (let i = 0; i < this.table.list.length; i++) {
            let n = this.table.list[i]
            if (n.messageStatus !== 'SUCCESS' && this.checkboxList[i] === false && n.applyStatus==='INITIAL') {
              return false
            }
          }
          return true
        },
        set: function (val) {
          let arr = []
          for (let i = 0; i < this.table.list.length; i++) {
            let n = this.table.list[i]
            if (n.messageStatus !== 'SUCCESS' && n.applyStatus==='INITIAL') {
              arr[i] = val
            } else {
              arr[i] = false
            }
          }
          this.checkboxList = arr
        }
      },
    },
    methods: {
      change (e) {
        this.files = e.target.files[0]
      },
      // 导入
      async impFun () {
        let file = this.files
        if (!file) {
          this.$message.warning('请选择上传文件')
          return
        }
        let formData = new FormData()
        formData.append('file', file)
        formData.append('month', this.month)
        let obj = {
          url: '/import/importTeacherApply.do',
          query: formData,
          el: '.btn-upload',
          noText: true
        }
        // 上传模板拿到返回的模板地址
        let res = await this.$ajax(obj)
        if (res.errMsgList && res.errMsgList.length) {
          this.$message.error(res.errMsgList[0])
        } else {
          this.$remind('导入成功')
          this.dialog = false
          this.$search(this.table)
        }
      },
      // 点击群发短信
      onClickSms () {
        this.dialogVisible = true
        this.checkboxList.forEach((item, idx) => {
          if (item) {
            this.form.phone.push(this.table.list[idx].phone)
          }
        })
      },
      // 短信
      async onSubmitInterview () {
        if (!this.form.phone.length){
          this.$message.warning('请先选择教师')
          return
        }
        await this.$refs.form.validate()
        await this.$ajax({
          url: '/teacherApply/sendTeacherApplySms.do',
          el: '.btn-Sms',
          noText: true,
          query: this.form
        })
        this.$remind('短信发送成功')
        this.dialogVisible = false
        this.$search(this.table)
      },
      // 档案入库
      async storage (item) {
        await this.$confirm(`您确认将【${item.name}】老师的档案入库吗?`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        await this.$ajax({
          url: '/teacherApply/teacherApplyDocIn.do',
          query: {
            id: item.teacherApplyId
          }
        })
        this.$remind('入库成功')
        this.$search(this.table)
      },
    },
    async mounted () {
      await this.$search(this.table)
      // 取消选择所有的多选框
      this.checkboxList = new Array(this.table.list.length).fill(false)
    },
  }
</script>
<style lang="sass" scoped>
    .btns
      position: absolute
      right: 15px
      top: 6px
      z-index: 9
    .dialog_btns
      margin-top: 15px
      text-align: center
</style>
