<template>
  <div>
    <search-box :searchData="searchData" :table="table"></search-box>
    <resize-table>
    <table class="table">
      <thead>
      <tr>
        <th width="40%">标题</th>
        <th width="20%">发布时间</th>
        <th width="20%">校区</th>
        <th width="20%">操作</th>
      </tr></thead>
      <tbody>
      <tr v-for="item in table.list">
        <td>{{item.title}}</td>
        <td>{{item.createdTime | substrTime }}</td>
        <td>{{item.campusName}}</td>
        <td>
          <span @click="$router.push(`/personnel/recruitment/publicity?id=`+item.id+``)" class="oper">编辑</span>
          <span @click="remove(item)" class="oper">删除</span>
        </td>
      </tr></tbody>
    </table>
    </resize-table>
    <no-data v-show="!table.noData"></no-data>
    <paging :data.sync="table"></paging>
  </div>

</template>
<script>
  export default {
    data() {
      return {
        searchData: [
          {type: 'select',title: '校区', model: 'campusId', option: []},
          {type: 'input', placeholder: '标题'}],
        table:{
          url: '/notice/queryTeacherRecruitNoticePage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            campusId: null,
            keywords: null
          }
        }
      }
    },
    methods: {
      // 获取所有校区
      async getCampus(){
        let arr = await this.$ajax({
          url: '/campus/queryCampusList.do'
        })
        this.searchData[0].option = []
        arr.forEach(e=>{
          this.searchData[0].option.push({
            value: e.id,
            label: e.name
          })
        })
      },
      // 删除招聘公告
      async remove(item){
        await this.$confirm(`您确认删除【${item.title}】吗?`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        await this.$ajax({
          url: '/notice/deleteTeacherRecruitNotice.do',
          query:{
            id: item.id
          }
        })
        this.$remind('删除成功')
        this.$search(this.table)
      },
    },
    mounted(){
      this.getCampus()
      this.$search(this.table)
    }
  }
</script>
